@import "../core/variables";
@import "../core/mixins";

.hero {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	height: 100vh;
	width: 100vw;
	background: url("../../images/back.png") no-repeat center;
	background-size: cover;
	text-align: center;
	padding: 50px 10px;
	font-family: $special;

	&__text {
		font-size: 30px;
		color: $blood;
	}
	&__logo {
		height: 200px;
	}
	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		background-color: $wine;
		height: 80px;
		width: 80px;
	}
	&__button {
		border: none;
		border-radius: 50%;
		height: 120px;
		width: 120px;
		background-color: $yellow;
		font-family: $special;
		font-size: 40px;
		color: $wine;
	}
}
